// The Vue build version to load with the `import` command
// (runtime-only or standalone) has been set in webpack.base.conf with an alias.
import Vue from 'vue'
import App from './App'
import store from './store'
import router from './router'
import {
   Row,
   Col,
   Button,
   Swipe, 
   SwipeItem,
   Icon,
   Lazyload,
   Tabbar,
   TabbarItem
} from 'vant';


Vue.use(Button)
   .use(Row)
   .use(Col)
   .use(Swipe)
   .use(SwipeItem)
   .use(Icon)
   .use(Lazyload)
   .use(Tabbar)
   .use(TabbarItem)

Vue.config.productionTip = false

/* eslint-disable no-new */
new Vue({
  el: '#app', 
  store,
  router,
  components: { App },
  template: '<App/>'
})



