<template>
   <div>

      <van-row class="search-bar">
         <van-col span="3" class="icon">
            <van-icon name="location" size="0.6rem"/>
         </van-col>
         <van-col span="16" class="input">
            <input type="text" placeholder="商城">
         </van-col>
         <van-col span="5" class="btn">
            <van-button type="primary" size="mini">搜索</van-button>
         </van-col>
      </van-row>


      <div class="swipe-box">
         <van-swipe :autoplay="2000">
            <van-swipe-item v-for="(item, index) in banner" :key="index">
               <img v-lazy="item.image" width="100%" />
            </van-swipe-item>
         </van-swipe>
      </div>
      
      <div class="category">
         <div class="item" v-for="(item,index) in category" :key="index">
            <img v-lazy="item.image" width="100%">
            <span>{{item.mallCategoryName}}</span>
         </div>
      </div>

      <div class="adverte">
         <img v-lazy="adverte" width="100%">
      </div>

      <div class="recommend">
         <h2>商品推荐</h2>
         <swiper :options="{slidesPerView: 'auto'}" style="border-top: 1px solid #ebebeb">  
            <swiper-slide class="recommend_box" v-for="(item,index) in recommend" :key="index"  style="border-right: 1px solid #ebebeb">
               <div class="Img">
                  <img v-lazy="item.image" :alt="item.goodsName" width="100%">
               </div>
               <p class="name">{{item.goodsName}}</p>
               <p class="mall-price">￥ {{item.mallPrice}}</p>
               <p class="price">￥ {{item.price}}</p>
            </swiper-slide>
         </swiper>
      </div>
   </div>
</template>

<script>
import 'swiper/dist/css/swiper.min.css'
import axios from 'axios'
import { swiper, swiperSlide } from "vue-awesome-swiper" 
export default {
   data() {
      return {
         banner: [],   
         category:[],
         adverte: "",
         recommend: []
      }
   },
   created(){
      // 获取数据
      axios.get('https://www.easy-mock.com/mock/5c9726dad1eeba73b8527408/shop/shop',{})
      .then(res=> {
         if(res.status==200){
            console.log( res.data );
            this.banner   = res.data.data.slides;
            this.category = res.data.data.category;
            this.adverte  = res.data.data.advertesPicture.PICTURE_ADDRESS;
            this.recommend = res.data.data.recommend;
         }
      })
      .catch(err => {
         console.error(err); 
      })
   },
   components: {
      swiper,
      swiperSlide
   }
}
</script>

<style lang="scss" scoped>
.search-bar {
   height: 90px;
   line-height: 90px;
   text-align: center;
   overflow: hidden;
   color: #ffffff;
   background-color: #e5017d;
   .van-icon {
      vertical-align: middle;   
   }
   input {
      display: inline-block;
      width: 100%;
      height: 52px;
      line-height: 52px;
      text-indent: 20px;
      font-size: 24px;
      background: #e5017d;
      border-bottom: 1px solid #ffffff;
   }
   input::-webkit-input-placeholder{
      color:#ffffff;
   }
   input::-moz-placeholder{   /* Mozilla Firefox 19+ */
      color:#ffffff;
   }
   input:-moz-placeholder{    /* Mozilla Firefox 4 to 18 */
      color:#ffffff;
   }
   input:-ms-input-placeholder{  /* Internet Explorer 10-11 */ 
      color:#ffffff;
   }

}

.swipe-box {
   max-height: 338px;
   overflow: hidden;
}
.category {
   background-color: #fff;
   margin: 0 12px 12px 12px;
   border-radius: 12px;
   display: flex;
   div {
      padding: 12px;
      font-size: 24px;
      text-align: center;
      flex: 1;
   }
}
.recommend {
   margin-top: 18px;
   background: #ffffff;
   h2 {
      color: #df825a;
      font-size: 26px;
      padding: 14px 22px 20px 22px;
   }
   .recommend_box {
      width: 250px;
      overflow: hidden;
      cursor: pointer;
      p.mall-price {
         font-size: 24px;
         color: red;
         margin-top: 8px;
         padding: 0 0 0 20px;
      }
      p.price {
         font-size: 22px;
         color: #bbb;
         text-decoration: line-through;
         padding: 0 0 14px 20px;
      }
      p.name {
         font-size: 24px;
         padding: 0 0 0 20px;
         overflow: hidden;
         text-overflow: ellipsis;
         white-space: nowrap;
      }
      &:last-child {
         border-right: none!important;
      }
   }
   .img {
      width: 100%;
      overflow: hidden;
      img {
         width: 100%;
         height: 100%;
      }
   }
  
}

</style>




